import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from "@/views/Login";
import Home from "@/views/Home";

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Login',
        component: Login,
        hidden: true
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
    }
]

const router = new VueRouter({
    // mode: 'history ',
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

// const originalPush = VueRouter.prototype.push

